.burgerMenu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 100;
  box-sizing: border-box;
  transform: translateX(100%);
  //background: grey;
  transition: 0.3s;

  &_show {
    transform: translateX(0%);
  }

  .inner {
    position: absolute;
    top: 20px;
    bottom: 20px;
    left: 20px;
    right: 20px;
    background: linear-gradient(85.96deg, rgba(255, 255, 255, 0) -20.51%, rgba(255, 255, 255, 0.05) 26.82%, rgba(255, 255, 255, 0) 65.65%), rgba(196, 196, 196, 0.05);
    box-shadow: 15px 10px 20px rgba(0, 0, 0, 0.1), inset 0px -2px 1px rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(30px);
    border-radius: 7px;
    padding: 28px 25px;
    box-sizing: border-box;

    display: flex;
    flex-direction: column;

    .link {
      text-decoration: none;
      font-weight: normal;
      font-size: 16px;
      line-height: 20px;
      text-transform: uppercase;

      &:not(:first-child) {
        margin-top: 24px;
      }

      &_light {
        color: #292D32;
      }

      &_dark {
        color: #FFF;
      }
    }

  }

}