@import "../../assets/scss/app";

.shopPage {
  position: relative;
  overflow: hidden;

  .wave {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;

    &:nth-child(1) {
      top: 0;
    }

    &:nth-child(2) {
      top: auto;
      bottom: -100px;
    }

  }

  .content {
    position: relative;
    width: 100%;
    height: 100%;
    z-index: 2;
  }


}