.homePage {
  position: relative;
  overflow: hidden;

  .wave {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;

    &:nth-child(1) {
      top: 0;
    }

    &:nth-child(2) {
      top: 2300px;
    }

    &:nth-child(3) {
      top: 4800px;
    }

    &:nth-child(4) {
      top: 7000px;
    }
  }

  .content {
    position: relative;
    width: 100%;
    height: 100%;
    z-index: 2;
  }

}