@import "../../assets/scss/app";

.faq {
  padding-top: 90px;

  .cards {
    max-width: 540px;
    margin: 28px auto 0;


    @include desktop() {
      margin-top: 72px;
    }

    .item {
      padding: 24px 0;
      border-radius: 14px;
      background: linear-gradient(87.74deg, rgba(255, 255, 255, 0) -15.35%, rgba(255, 255, 255, 0.05) 15.89%, rgba(255, 255, 255, 0) 41.52%), rgba(196, 196, 196, 0.05);
      box-shadow: 15px 10px 20px rgba(0, 0, 0, 0.1), inset 0px -2px 1px rgba(255, 255, 255, 0.1);
      backdrop-filter: blur(30px);

      &:not(:first-child) {
        margin-top: 16px;
      }

      .title {
        font-weight: bold;
        font-size: 20px;
        line-height: 24px;
        padding-left: 18px;
        padding-right: 70px;
      }

      .text {
        margin-top: 15px;
        font-weight: normal;
        font-size: 15px;
        line-height: 18px;
        padding-left: 40px;
        padding-right: 20px;
      }





    }
  }
}