@import "../../../assets/scss/app";

.shopItems {
  padding-top: 48px;

  @include desktop() {
    padding-top: 53px;
  }

  .menu {
    margin-top: 23px;

    @include desktop() {
      margin-top: 13px;
    }

    .inner {
      margin-left: -10px;
      margin-right: -10px;
      display: flex;
      flex-wrap: wrap;

      @include desktop() {
        margin-left: -20px;
        margin-right: -20px;
      }

      .item {
        cursor: pointer;
        padding: 10px 20px;
        border-radius: 37px;
        font-weight: bold;
        font-size: 30px;
        line-height: 37px;
        margin: 20px 10px 0;

        @include desktop() {
          padding: 20px 90px;
          margin: 30px 20px 0;
        }

        p {
          font-weight: bold;
          font-size: 24px;
          line-height: 120%;

          @include desktop() {
            font-size: 30px;
          }
        }

        &_light {
          box-shadow: inset -3px -3px 5px #FFFFFF, inset 3px 3px 5px #D6D6E1;

          p {
            color: #292D32;
            opacity: 0.2;
          }
        }

        &_dark {
          box-shadow: inset -3px -3px 5px #30343A, inset 3px 3px 5px #24262C;

          p {
            color: #FFF;
            opacity: 0.2;
          }
        }

        &_selected_light {
          box-shadow: -3px -3px 5px #FFFFFF, 0px 0px 5px #D6D6E1;

          p {
            opacity: 1;
          }
        }

        &_selected_dark {
          box-shadow: -3px -3px 5px #30343A, 3px 3px 5px #24262C;

          p {
            opacity: 1;
          }
        }


      }
    }
  }

  .cards {
    display: flex;
    flex-direction: column;

    @include desktop() {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-column-gap: 30px;
    }

    .card {
      padding: 20px;
      backdrop-filter: blur(30px);
      border-radius: 7px;
      margin-top: 50px;
      background: linear-gradient(85.96deg, rgba(255, 255, 255, 0) -20.51%, rgba(255, 255, 255, 0.05) 26.82%, rgba(255, 255, 255, 0) 65.65%), rgba(196, 196, 196, 0.05);
      box-shadow: 15px 10px 20px rgba(0, 0, 0, 0.1), inset 0px -2px 1px rgba(255, 255, 255, 0.1);

      img {
        display: block;
        margin: -40px auto 0;
        width: 466px;
        height: 313px;
        object-fit: cover;

        @include desktop() {
          width: 320px;
          height: 313px;
          margin: -40px auto 0;
        }
      }

      .title {
        margin-top: 20px;
        font-weight: 500;
        font-size: 20px;
        line-height: 24px;
        text-align: center;
      }

      .price {
        margin-top: 7px;
        font-weight: bold;
        font-size: 40px;
        line-height: 49px;
        text-align: center;
      }

      .buttons {
        margin-top: 14px;
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: 42px;

        @include desktop() {
          grid-column-gap: 29px;
        }

        .btn {
          height: 45px;
          @include button();
          display: flex;
          justify-content: center;
          align-items: center;
          border-radius: 8px;
          box-sizing: border-box;
          border: 1px solid rgba(131, 131, 131, 0.3);
          filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));

          p {
            margin-left: 5px;
          }

          .icon {
            width: 24px;
            height: 24px;

            svg {
              width: 24px;
              height: 24px;
            }

            &_dark svg path {
              fill: #FFF;
            }
          }




        }
      }




    }

  }

}